.displayFlexRowStart {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
}

.displayFlexRowCenter {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.displayFlexColCenter, .card-header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.card {
  border: 2px solid #029688;
}

.card-header {
  background-color: #029688;
  color: white;
}

.divMssCards, .divMenuMssCards {
  background-color: white;
  padding: 1.5rem;
}

.divMenuMssCards {
  margin-bottom: 2rem;
}

