@use "custom" as *;
#root{
    /*background-color: #222;*/
    background-color: silver;
    overflow-x:hidden;
}   
.container-fluid{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}  
footer{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}    
nav#sidebar{
    width: 250px;
    overflow-y: auto;
    list-style: none;
}
@media only screen and (height: 1020px) {
    nav#sidebar{
        width: 250px; 
        max-height: calc(100vh - 230px);
        overflow-y: auto;
    }
}
@include media-breakpoint-down(sm){
    nav#sidebar{
        width: 250px;
        overflow-y: auto;
        margin-top: -5.5rem;
    }
    ol.breadcrumb li.breadcrumb-item, ol.breadcrumb li.lirmsvg{
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
    }
}
.sidebar {   
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    z-index: 100;
    padding: 90px 0 0;
    box-shadow: inset -1px 0 0 rgba(0, 0, 0, .1);
    z-index: 99;
    /*background-color: #5a5c69 !important;*/
    background-color: #000000 !important;
}

@media (max-width: 767.98px) {
.sidebar {
    top: 11.5rem;
    padding: 0;
}
}

.navbar {
    box-shadow: inset 0 -1px 0 rgba(0, 0, 0, .1);
}

@media (min-width: 767.98px) {
.navbar {
    top: 0;
    position: sticky;
    z-index: 999;
}
.sidebar{
    width: 250px;
}
}

.sidebar .nav-link {
color: #333;
}

.sidebar .nav-link.active {
color: #0d6efd;
}
span.ml-2{
    color: white;
}
svg.feather{
    color: lightgreen;
}
.sidebar-divider{
    color: white;
    border: 1.5px solid white;
    opacity: 1 !important;
}
a.mssDashMenuSubmenu::after{
    display: block;
    flex-shrink: 0;
    width: 1.25rem;
    height: 1.25rem;
    margin-left: auto;
    content: "";
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%23ffffff'%3e%3cpath fill-rule='evenodd' d='M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z'/%3e%3c/svg%3e");
    background-repeat: no-repeat;
    background-size: 1.25rem;
    transition: transform .2s ease-in-out;
    color: white;
}

span#closenav{
    padding-left:3px;
}

.sidebar .nav-link.active {
    display:flex;
    flex-direction:row;
    justify-content:flex-start;
    align-items:flex-start;
    color: white !important;
}

.collapse-inner{
    background-color: #222;
    display:flex;
    flex-direction:row;
    justify-content:space-between;
    align-items:flex-start;
    color: white !important;
}

.mssDashMenu{
    background-color: #222;
    display:flex;
    flex-direction:row;
    justify-content: flex-start !important;
    align-items: flex-start !important;
    color: white !important;
}

.dollars{
    display:flex;
    flex-direction:row;
    justify-content:flex-start;
    align-items:flex-start;
    gap:.5px;
}
.dollars i{
    margin-left: -5px;
}