$bg-cards: #029688;
$col-cards: white;

.displayFlexRowStart{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
}

.displayFlexRowCenter{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.displayFlexColCenter{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.card{
    border: 2px solid $bg-cards;
}

.card-header{
    @extend .displayFlexColCenter;
    background-color: $bg-cards;
    color: $col-cards;
}

.divMssCards{
    background-color: $col-cards;
    padding: 1.5rem;
}

.divMenuMssCards{
    @extend .divMssCards;
    margin-bottom: 2rem;
}