@use "custom" as *;
@use 'mixins' as *;
@use 'fontawesome' as *;

.menu {
    @include gradient;


    li {
      padding: 0 0.7rem;

      a {
        color: $white;
        padding: 0 0.7rem;
        text-transform: capitalize;
        font-weight: 600;      

      }
      &:hover {
        color: $white;
      }
  
    }
    
}

.btn-rounded{
    //for hidden:
    /*
    @include media-breakpoint-down(sm){
      display:none;
    }

    @include media-breakpoint-up(sm){
      display:none;
    }*/

    position: relative;
    padding: 0.9375rem 4.375rem 0.9375rem 1.2rem;
    font-weight: 500;
    margin: 0;
    border: 0.1rem solid rgba($white, 0.6);
    background-color: $white;

    span{
        position: absolute;
        background-color: $primary;
        width: 2.5rem;
        height: 2.5rem;
        top:50%;
        right:5px;
        transform: translateY(-50%);
        border-radius: 50%;
    }

    i{
        color:$white;
        font-size:1rem;
        line-height: 2.5rem;
    }

    &:hover{
        @include gradient;
        color:$white;
    }
}

.display-2 {
  margin-bottom: 1rem;
  text-transform: capitalize;
  font-family: 'Poppins', sans-serif;

  &--intro {
      display: inline-block;
      font-weight: 700;
      font-family: 'Poppins', sans-serif;
  }

  &--description {
      font-size: 1rem;
      display: block;
      margin-top: 1.2rem;
      text-transform: none;

      @include media-breakpoint-up(lg) {
          font-size: 1.4rem;
          display: inline-block;

      }
  }
}

// HEADING LINE 
.heading-line::before {
  content: "";
  width: 10rem;
  height: 0.1rem;
  display: block;
  margin: 0 auto;
  background-color: $primary;
}
.heading-line::after {
  content: "";
  width: 2rem;
  padding-top: 0.5rem;
  height: 0.2rem;
  display: block;
  margin: 0 auto;
  margin-bottom: 1rem;
  background-color: $primary;
}

// DISPLAY 3 
.display-3 {
  font-family: 'Poppins', sans-serif;

  &--title {
      font-size: 1.2rem;
      text-transform: capitalize;
      font-weight: 600;
      font-family: 'Poppins', sans-serif;
      display: inline-block;
      background-image: linear-gradient(to right, $primary, $secondary);
      background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;

      @include media-breakpoint-up(lg) {
          font-size: 1.8rem;
      }


  }
}

// BORDER RIGHT 
.border-right {
  border-right: 0;

  @include media-breakpoint-up(sm) {
      border-right: 0.1rem solid $gray-300;
      height: 100%;
  }
}


// GRADIENT BG 
.gradient  {
  background: linear-gradient(to right, rgba($primary, 0.95), rgba($secondary, 0.95));
}

// PULSE BORDER 

@keyframes pulse-border {
  0% {
      transform: scale(1);
      opacity: 1;
  }

  100% {
      transform: scale(1.5);
      opacity: 0;
  }
}

.intro-section {
  @include gradient;
  padding: 10rem 0 0 0;
  width: 100%;
  height: 100%;
}

.intros {


  .video-box {
      position: relative;


      a {
          // @include absoluteCenter;

          span {


              i {

                  font-size: 6rem;
                  color: $secondary;

              }
          }
      }
  }
}

.border-animation {
  position: absolute;
  top: 0;
  left: 0;
  width: 6rem;
  height: 6rem;
  border: 0.55rem solid $white;
  border-radius: 50%;
  animation: pulse-border 1.5s linear infinite;

  &--border-1 {
      animation-delay: 1s;
  }
  &--border-2 {
      animation-delay: 1.5s;
  }
}

.campanies {
  padding:  0 0 5rem 0;

  &__logo-box {
      max-width: 100%;
      height: 3rem;
      margin-bottom: 2rem;
      padding: 0.5rem;
      text-align: center;
      position: relative;

      img {
          height: 100%;

          &:hover {
              filter: grayscale(120);
          }
      }
  }
}

.services {
  position: relative;
  padding: 1rem 0;

  &__content {
      padding: 1rem;
      margin-top: 0;

      p {
          font-size: 0.8rem;

          @include media-breakpoint-up(lg) {
              font-size: 1rem;
          }
      }

      @include media-breakpoint-up(lg) {
          margin-top: 5rem;
      }

      .icon {
          font-size: 1.5rem;
          color: $primary;

          @include media-breakpoint-up(lg) {
              font-size: 2.5rem;
          }
      }
  }

  &__pic {
      max-width: 100%;
  }
}

.testimonials {
  position: relative;
  padding: 0;
  @include gradient;

  &__card {
      background-color: rgba($white, 0.8);
      padding: 2rem 1.25rem;
      border-radius: 0.625rem;
      height: auto;
      text-align: center;

      i {
          font-size: 1.5rem;
          color: rgba($primary, 0.7);
      }

      .ratings {


          i {
              color: rgba($primary, 0.7);
              font-size: 1rem;
          }
      }
  }

  &__picture {
      width: 6rem;
      height: 6rem;
      position: absolute;
      left: 50%;
      transform: translate(-50%, -50%);

      img {
          border: 0.325rem solid rgba($primary, 0.7);
      }
  }

  &__name{
      padding-top: 3rem;
      margin-bottom: 1rem;
      text-align: center;
      font-weight: 500;
      color: $white;

      h3 {
          padding-top: 0.8rem;
          text-transform: capitalize;
      }

      p {
          padding-bottom: 2rem;
          text-transform: capitalize;
      }
  }
}

.footer {
  padding-top: 1rem;
  background-color: $footer;
  margin-top: 4rem;
}

// STYLE THE CONTACT-BOX CONTENT 
.contact-box {
  position: relative;

  &__icon {
     width: 2rem;
     stroke: $primary; 
     padding-top: 0.5rem;

     @include media-breakpoint-up(sm) {
      width: 4rem;
     }
  }


  &__info {
      padding: 0.4rem;
      font-weight: 600;
      


      &--title {
          color: $white;
          text-decoration: none;
          font-size: 1rem;

          @include media-breakpoint-up(sm) {
              font-size: 1.4rem;
             }

      }

      &--subtitle {
          font-size: 0.8rem;
          color: $gray-600;
          text-decoration: none;

          @include media-breakpoint-up(sm) {
              font-size: 1rem;
             }
          
      }
  }
}

// STYLE THE SOCIAL MEDIA CONTENT 
.footer-sm {


  a {
      color: $white;

      i {
          font-size: 1rem;
          margin-left: 1rem;

          @include media-breakpoint-up(sm) {
              font-size: 1.6rem;
             }


      }
      &:hover {
          color: $primary;
      }
  }
}

// STYLE THE CAMPANY INFO CONTENT 
.campany-list {
  display: block;
  list-style-type: none;
  position: relative;

  li {
      margin-bottom: 0.5rem;
      text-decoration: none;

      a {
          color: $white;
          text-decoration: none;

          &:hover {
              color: $primary;
          }
      }
  }
}

// STYLE THE COPYRIGHT  CONTENT 
.footer-bottom {
  //background-color: $black;

  &__copyright {
      color: $gray-600;
      font-weight: 400;
      

      a {
          color: $gray-300;
          text-decoration: none;

          &:hover {
              color: $primary;
              text-decoration: underline;
           
          }
      }
  }
}


// STYLE THE BACK TO TOP BUTTON 
.back-to-top {
  position: fixed;
  width: 3rem;
  height: 3rem;
  right: 1.875rem;
  bottom: 1.875rem;
  z-index: 9999;

  i {
      color: $white;
      font-size: 1rem;
      @include absoluteCenter;
  }
}
//home page end

//forms
form .form-group{
    margin: 2px;
}

form button{
    margin: 2px;
}
//forms end